h1 {
  text-align: center;
}
.media {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}
.item {
  /* border: 1px solid white; */
  width: 250px;
  text-align: center;
}
.poster img {
  width: 200px;
  height: 300px;
  object-fit: cover;
}
.title {
  color: white;
}
.type {
  display: inline-block;
  background-color: lime;
  border-radius: 15px;
  color: #1a1a1a;
  text-transform: capitalize;
  font-weight: bold;
  font-size: 80%;
  padding: 2px 6px 3px 6px;
  margin: 5px 0;
}
.pagination {
  margin: 20px 0;
  text-align: center;
}
.pagination button {
  border: none;
  padding: 10px 20px;
  background-color: lime;
  border-radius: 10px;
  color: #1a1a1a;
  font-size: 1em;
  margin: 0 3px;
  transition: all 200ms;
}
.pagination button:hover {
  background-color: white;
  transform: translateY(3px);
}
.pagination button:disabled {
  opacity: 0.25;
}